
.app-navbar{position:fixed;min-width:100%;z-index:1024;box-shadow:0 2px 3px rgba(17,17,17,0.1),0 0 0 1px rgba(17,17,17,0.1)
}
.app-navbar .container{margin:auto 10px
}
.app-navbar .nav-right{-ms-flex-align:stretch;align-items:stretch;align-items:stretch;-ms-flex:1;flex:1;-ms-flex-pack:end;justify-content:flex-end;overflow:hidden;overflow-x:auto;white-space:nowrap
}
.hero .nav{background:#ffffff
}
.hero-brand .vue{margin-left:10px;color:#36AC70
}
.hero-brand .admin{color:#28374B
}
.scaleFade-enter-active,.scaleFade-leave-active{opacity:1;-ms-transform:scale(1);transform:scale(1);transition:all .25s ease-in-out
}
.scaleFade-enter,.scaleFade-leave-active{opacity:0;-ms-transform:scale(1.1);transform:scale(1.1)
}
.slideUp-enter-active,.slideUp-leave-active{opacity:1;-ms-transform:translate(0, 0) scale(1);transform:translate(0, 0) scale(1);transition:all .25s ease-in-out
}
.slideUp-enter,.slideUp-leave-active{opacity:0;-ms-transform:translate(0px, -100px) scale(0.6);transform:translate(0px, -100px) scale(0.6)
}



